import { SceneElement } from "../../engine/core/SceneElement";
import { Line } from "../primitives/Line";
import { DrawLineCom } from "../../engine/general-components/DrawLineCom";
import { SceneContext } from "../../engine/core/SceneContext";
import { Vector } from "../primitives/Vector";
import { Style } from "../core/Style";

export class Grid extends SceneElement{
    constructor(view : SceneContext, gap: number){
        super(view);
        this.Priority = -1000;
        this.Style.lineWidth = 0.2;
        this.Style.strokeStyle = "black";
        this.Position = 'absolute';
        this.Transition = view.Center;

        let {Width, Height} = view;
        let x_c = view.Center.x;
        let y_c = view.Center.y;

        this.AddComponent(new DrawLineCom(() => this.GetLongitudes(gap)));
        this.AddComponent(new DrawLineCom(() => this.GetLatitudes(gap)));

        this.AddComponent(new DrawLineCom(() => new Line(
            new Vector(- Width,  (<SceneElement>this.Parent).Transition.y * this.Scale.x), 
            new Vector(Width,  (<SceneElement>this.Parent).Transition.y * this.Scale.x)
        ), new Style({
            lineWidth:1
        })))

        this.AddComponent(new DrawLineCom(() => new Line(
            new Vector( (<SceneElement>this.Parent).Transition.x * this.Scale.x, - Height), 
            new Vector( (<SceneElement>this.Parent).Transition.x * this.Scale.x, Height)
        ), new Style({
            lineWidth:1
        })))
    }

    private GetLatitudes(gap: number){
        let {Width, Height} = this.Scene;
        let arr : Line[] = [];
        let position = this.Parent?this.Parent.Transition : new Vector(0,0);
        gap = this.Parent ? this.Parent.Scale.x : gap;
        let x = position.x % gap;
        
        while(x < Width){
            let _x = x;
            arr.push(new Line(
                new Vector(_x, 0), 
                new Vector(_x, Height)
            ));
            x += gap;
        }
        return arr;
    }

    private GetLongitudes(gap: number){
        let {Width, Height} = this.Scene;
        if(!this.Parent)
            return [];
        let arr : Line[] = [];
        let position = this.Parent?this.Parent.Transition : new Vector(0,0);
        gap = this.Parent ? this.Parent.Scale.y : gap;
        let y = position.y % gap;
        
        while(y < Height){
            let _y = y;
            arr.push(new Line(
                new Vector(0, _y), 
                new Vector(Width, _y)
            ));
            y += gap;
        }
        return arr;
    }
}